import { observable, action } from "mobx";

import { GuessedTricks } from "./GuessedTricks";
import { Score } from "./Score";
import { Step } from "./Step";
import store from "../..";

export class Round {
  @observable roundNumber: number;
  @observable guessOrder: string[];
  @observable amountOfTricks: number;

  @observable step: Step = Step.Guess;

  @observable guessIndex: number = 0;
  @observable guessedTricks: GuessedTricks[] = [];

  @observable scoreIndex: number = 0;
  @observable scores: Score[] = [];

  constructor(
    roundNumber: number,
    guessOrder: string[],
    amountOfTricks: number
  ) {
    this.roundNumber = roundNumber;
    this.guessOrder = guessOrder;
    this.amountOfTricks = amountOfTricks;
  }

  @action
  guessTricks = (amount: number) => {
    if (amount == null || amount < 0 || amount > this.amountOfTricks) {
      return;
    }

    // The tricks can't exactly be the totalTricks
    if (this.guessIndex === this.guessOrder.length - 1) {
      const totalTricks = this.guessedTricks.reduce(
        (accumulator, guessedTrick) => accumulator + guessedTrick.amount,
        0
      );

      if (totalTricks + amount === this.amountOfTricks) {
        return;
      }
    }

    this.guessedTricks.push({
      playerId: this.guessOrder[this.guessIndex],
      amount
    });

    this.guessIndex++;

    if (this.guessIndex === this.guessOrder.length) {
      this.step = Step.Play;
    }
  };

  @action
  setScores = () => {
    this.step = Step.Score;
  };

  @action
  setScore = (correct: boolean, tricks: number) => {
    let calcTricks;

    if (correct) {
      calcTricks = this.guessedTricks[this.scoreIndex].amount;
    } else {
      if (tricks == null) return;
      calcTricks = tricks ? tricks : 1;
    }

    this.scores.push({
      playerId: this.guessOrder[this.scoreIndex],
      score: correct ? 10 + calcTricks * 2 : calcTricks * 2 * -1
    });

    this.scoreIndex++;

    if (this.scoreIndex >= this.guessOrder.length) {
      store.game.nextRound();
    }
  };
}
