import { observable, action, computed } from "mobx";

import { Howl } from "howler";

import store from "../index";

import { Player } from "./Player";
import { Round } from "./Round";

import shuffle from "../../utils/shuffle";

const attack = new Howl({
  src: [`${process.env.PUBLIC_URL}/audio/attack.mp3`]
});

const yeah = new Howl({
  src: [`${process.env.PUBLIC_URL}/audio/yeah.mp3`]
});

export class Game {
  @observable roundNumber: number = 0;
  @observable roundHistory: Round[] = [];
  @observable minTricks: number = 1;
  @observable maxTricks: number = 10;
  @observable players: Player[] = [];
  @observable playerOrder: string[] = [];
  @observable roundTricks: number[] = [];
  @observable loopTricksBack: boolean = true;

  constructor() {
    this.addPlayer("Stefan");
  }

  @action
  setMinTricks = (tricks: number): void => {
    if (tricks < 1 || tricks > this.maxTricks) return;

    this.minTricks = tricks;
  };

  @action
  setMaxTricks = (tricks: number): void => {
    if (tricks < this.minTricks) return;

    this.maxTricks = tricks;
  };

  @action
  addPlayer = (name: string): void => {
    const player = new Player(name);
    this.players.push(player);
    this.playerOrder.push(player.id);
  };

  @action
  removePlayer = (id: string): void => {
    this.players = this.players.filter(player => player.id !== id);
    this.playerOrder = this.playerOrder.filter(orderId => orderId !== id);
  };

  @action
  resetGame = (): void => {
    this.roundNumber = 0;
    this.roundHistory = [];
    this.minTricks = 1;
    this.maxTricks = 10;
    this.players = [];
    this.playerOrder = [];
  };

  @action
  startGame = (): void => {
    if (this.players.length < 2) return;

    for (let i = this.minTricks; i <= this.maxTricks; i += 1) {
      this.roundTricks.push(i);
    }

    if (this.loopTricksBack) {
      for (let i = this.maxTricks; i >= this.minTricks; i -= 1) {
        this.roundTricks.push(i);
      }
    }

    this.playerOrder = shuffle(this.playerOrder);
    this.nextRound();
    store.router.updateRoute("round");
  };

  @action
  nextRound = (increment: boolean = true): void => {
    if (this.roundNumber >= this.roundTricks.length) {
      yeah.play();
      store.router.updateRoute("finished");
      return;
    }

    attack.play();

    if (increment) this.roundNumber += 1;

    const shuffleOffset: number = (this.roundNumber - 1) % this.players.length;
    const guessOrder: string[] = [];

    for (let i = 0; i < this.playerOrder.length; i += 1) {
      const pointer = (i + shuffleOffset) % this.playerOrder.length;
      guessOrder.push(this.playerOrder[pointer]);
    }

    const round = new Round(
      this.roundNumber,
      guessOrder,
      this.roundTricks[this.roundNumber - 1]
    );

    this.roundHistory.push(round);
  };

  @action
  resetRound = (): void => {
    this.roundHistory.pop();
    this.nextRound(false);
  };

  @computed get currentRound(): Round {
    return this.roundHistory[this.roundHistory.length - 1];
  }
}
