import React, { useState } from "react";
import { observer } from "mobx-react-lite";

import { Game } from "../../../stores/Game";

interface IProps {
  game: Game;
}

export const Scores = observer(({ game }: IProps) => {
  const [tricksWrong, setTricksWrong] = useState("");
  const {
    players,
    currentRound: { scoreIndex, guessOrder, setScore, guessedTricks }
  } = game;

  const playerToSetScore = players.find(
    player => player.id === guessOrder[scoreIndex]
  );

  const guessedTrick = guessedTricks.find(
    guessedTrick => guessedTrick.playerId === guessOrder[scoreIndex]
  );

  if (!playerToSetScore || !guessedTrick) return null;

  const setRoundScore = (correct: boolean) => {
    if (tricksWrong === "" && !correct) return;
    setScore(correct, parseInt(tricksWrong, 10));
    setTricksWrong("");
  };

  return (
    <>
      Behaalde score voor: <b>{playerToSetScore.name}</b>. Verwachte slagen:{" "}
      <b>{guessedTrick.amount}</b>
      <div className="row">
        <div className="col-sm">
          <button
            className="btn btn-success btn-block"
            type="button"
            onClick={() => setRoundScore(true)}
          >
            Goed
          </button>
        </div>
        <div className="col-sm">
          <div className="input-group mb-3">
            <input
              type="number"
              className="form-control"
              placeholder="Slagen ernaast"
              value={tricksWrong}
              onChange={e => setTricksWrong(e.target.value)}
            />
            <div className="input-group-append">
              <button
                className="btn btn-danger"
                style={{ padding: "0 4rem" }}
                type="button"
                onClick={() => setRoundScore(false)}
              >
                Fout
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
