import React, { useState } from "react";
import { observer } from "mobx-react-lite";

import { Game } from "../../../stores/Game";

interface IProps {
  game: Game;
}

export const ExpectedTricks = observer(({ game }: IProps) => {
  const [expectedTricks, setExpectedTricks] = useState("");
  const {
    players,
    currentRound: { guessIndex, guessOrder, guessTricks }
  } = game;

  const playerToGuess = players.find(
    player => player.id === guessOrder[guessIndex]
  );

  if (!playerToGuess) return null;

  const guessExpectedTricks = () => {
    if (expectedTricks === "") return;
    guessTricks(parseInt(expectedTricks, 10));
    setExpectedTricks("");
  };

  return (
    <>
      <div className="row">
        <div className="col-sm">
          <p className="text-left">
            Verwachte slagen voor <b>{playerToGuess.name}</b>
          </p>
        </div>
      </div>
      <div className="row">
        <div className="col-sm">
          <div className="input-group mb-3">
            <input
              type="number"
              className="form-control"
              placeholder="Verwachte aantal slagen"
              value={expectedTricks}
              onChange={e => setExpectedTricks(e.target.value)}
            />
            <div className="input-group-append">
              <button
                className="btn btn-primary"
                type="button"
                onClick={guessExpectedTricks}
              >
                Toevoegen
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});
